<template>
  <div class="persin">
    <!-- 导航 -->
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <!-- 内容 -->
    <div class="content2">
      <div class="leftmenus">
        <div class="menus">
          <div
            class="menus-item"
            :class="[ index === menusIndex ? 'active' :　'' ]"
            v-for="(item, index) in menus"
            :key="index"
            @click="path(item.path, index)"
          >
            <img :src="item.icon" />
            <span>{{ item.title }}</span>
          </div>
        </div>
      </div>
      <div class="rightcontent">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";


export default {
  components: {
    AoNav
  },
  data() {
    return {
      menusIndex: 0,
      menus: this.$t('person.index_menus')
    };
  },
  mounted() {},
  methods: {
    path(path, index) {
      this.menusIndex = index
      this.$router.push({ path: path })
    }
  }
};
</script>

<style lang="scss">
.persin {
  background-color: #273041;
  .content2 {
    width: 1200px;
    margin: 0 auto;
    padding-top: 30px;
    min-height: 880px;
    overflow: hidden;
  }
  .leftmenus {
    margin-right: 20px;
    height: 350px;
    border-radius: 3px;
    padding: 4px;
    float: left;
    background-color: #1a243b;
    width: 210px;
    margin-bottom: -10000px;
    padding-bottom: 10000px;
    .menus {
      font-size: 14px;
    }
    .menus-item {
      height: 54px;
      line-height: 54px;
      padding-left: 38px;
      margin-bottom: 4px;
      cursor: pointer;
      img {
        margin-right: 12px;
        margin-top: -4px;
        width: 20px;
        vertical-align: middle;
      }
      span {
        color: #c7cce6;
      }
      &.active {
        color: #5697f4;
        background-color: #273142;
        border-bottom: none;
        display: block;
        span {
          color: #5697f4;
        }
      }
    }
  }
  .rightcontent {
    padding-left: 34px;
    padding-right: 34px;
    padding-top: 34px;
    background-color: #1a243b;
    width: 970px;
    min-height: 800px;
    float: right;
    padding-bottom: 20px;
  }
}
</style>



